<template>
	<ul>
		<li v-for="item in list">{{item}}</li>
	</ul>
</template>
<script>
export default{
	name:'wlj',
	data(){
		return {
			list:[
				"apple",
				"banana",
				"pear",
				"orange"
			]
		}
	}
}
</script>
<style>
	li{ line-height: 30px; border-bottom:1px dashed red; }
</style>


