import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import wlj from '@/components/wlj'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
   	  path: '/wanglaoji',
      name: 'Hello',
      component: wlj
    }
  ]
})



